import React from "react";

const styles = {
    display: "flex",
    flexDirection: "row",
    gap: 10,
    alignItems: "center",
};

export default function CommentList({ comments }) {
    return (
        <>
            <h3>Comments: </h3>
            {comments.map((comment, key) => (
                <div key={key} style={styles}>
                    <h4>{comment.user || "unknown"}:</h4>
                    <p>{comment.text}</p>
                </div>
            ))}
        </>
    );
}
