import React from "react";

export default function UpvoteSection({
    articleName,
    upvotes,
    setArticleInfo,
}) {
    const upvote = async () => {
        const result = await fetch(`/api/articles/${articleName}/upvote`, {
            method: "post",
        });

        const body = await result.json();
        setArticleInfo(body);
    };
    return (
        <>
            <button onClick={upvote}>Add Upvote</button>
            <p>This post has {upvotes} upvotes</p>
        </>
    );
}
